import { useEffect, useState } from "react";


export const SelectModal = ({ props }) => {

    const [options, setOptions] = useState([]);

    useEffect(() => {
        setOptions(props.initialOptions)
    }, [props.initialOptions])

    const handleSearch = (event) => {
        let value = event.target.value;
        const res = props.initialOptions.filter(item => item.toLowerCase().includes(value.toLowerCase()))
        res.length > 0 ? setOptions(res) : setOptions(['Aucun resultat trouvé'])
    }

    const closeSelectModal = () => {
        document.querySelector('.modal').classList.remove(props.modalClass);
    }

    const selectOption = (value) => {
        props.handleChange({ target: { name: props.selectName, value: value } })
        closeSelectModal();
    }

    return <div className={"modal"}>
        <div className="selectModal modalBody">
            <p className="modalCloser" onClick={() => closeSelectModal()}>
                <i className="mdi mdi-close"></i></p>
            <div className="searchPart">
                <input type="search" placeholder="Chercher ici..."
                    onChange={(event) => handleSearch(event)} />
            </div>
            <h3>Selectionnez {props.selectTitle} </h3>
            <div className="selectList">
                {
                    options.map(option => <section key={option}>
                        <span>{option} </span>
                        {option != 'Aucun resultat trouvé'
                            ? < button onClick={() => selectOption(option)}> Choisir</button>
                            : ''}
                    </section>)
                }
            </div>
        </div>
    </div >
}