const CourseCard = ({ props }) => {
    return (<div className="courseCard">
        <section className="ccHead">
            <span>{props.typeCours}</span>
            <img src={require('../Assets/images/profils/' + props.auteurPic).default}
                className="rounded" />
        </section>
        <section className="ccBody">
            <p className="ccbFilière">{props.filière}</p>
            <h3 className="ccbTitle">{props.title}</h3>
            <p className="ccbUniversity">{props.université}</p>
            <div className="ccbFoot">
                <span>2 <i className="mdi mdi-eye"></i> </span>
                <span>3 <i className="mdi mdi-comment"></i> </span>
                <span>4 <i className="mdi mdi-heart"></i> </span>
                <span>1 <i className="mdi mdi-file"></i> </span>
            </div>
        </section>
    </div>)
}

export default CourseCard;