import { Link } from "react-router-dom"

const DashboardNavBar = () => {

    const showNavMenu = () => {
        document.querySelector('.ducProfilPic').classList.toggle('ducProfilPicClicked')
        document.querySelector('.ducOptions').classList.toggle('ducOptionsVisible')
    }

    return (<nav className="dashboardNav">
        <div className="dnLogo">
            <img src={require("../Assets/images/logo.webp").default} />
        </div>
        <div className="dnUserConnected">
            <i className="mdi mdi-bell ducNotice" ></i>
            <img className="rounded ducProfilPic"
                src={require("../Assets/images/illustrations/profile_female_f.svg").default}
                onClick={() => showNavMenu()} />
            <div className="ducOptions">
                <span>johsnonevans686@gmail.com</span>
                <Link to="">Votre profil</Link>
                <Link to="">Paramètre</Link>
                <Link to="">Deconnexion</Link>
            </div>
        </div>

    </nav>)
}

export default DashboardNavBar;