export const FriendConnected = () => {
    const randomsUser = [1, 2, 3, 4, 5, 6, 1, 2, 3,]

    return (<div className="friendConnected">
        <h2>Camarade connecté(es)</h2>
        <div className="fcList">
            <div className="fclItems horizontalScrollTabs">
                {
                    randomsUser.map((ru, index) =>
                        <div className="fcItem" id={index} key={index}>
                            <img className="rounded"
                                src={require('../Assets/images/profils/random1.jpg').default} />
                            <span>Jhon wick </span>
                        </div>
                    )
                }
            </div>
            {<ScrollTabsNav props={{ container: '.fclItems', scrollWidth: 150 }} />}
        </div>
        <div className="viewAll">
            <Link to="">Voir tout</Link>
        </div>
    </div>)
}

export const CoursesPostedList = () => {
    const [selectedOption, setSO] = useState(0);

    return (<div className="coursePostedList">
        <h2>Dernier cours publiés</h2>
        <div className="clOptions horizontalScrollTabs">
            {
                courseListOptions.map((item, index) =>
                    <span key={index}
                        className={selectedOption == index ? "selectedOption" : ""}
                        onClick={(event) => selectTabsOption(event, index, setSO)}>{item}</span>)
            }
        </div>
        <div className="cplList">
            <div className="cpllItems horizontalScrollTabs">
                {
                    fakeCourses.map((item, index) => <CourseCard props={randomCourse} key={index} />)
                }
                {<ScrollTabsNav props={{ container: '.cpllItems', scrollWidth: 215 }} />}
            </div>
        </div>
    </div>)
}

export const CoursesAskedList = () => {
    const [selectedOption, setSO] = useState(0);
    return (<div className="courseAskedList">
        <h2>Dernier cours demandés</h2>
        <div className="clOptions horizontalScrollTabs">
            {
                courseListOptions.map((item, index) =>
                    <span key={index} className={selectedOption == index ? "selectedOption" : ""}
                        onClick={(event) => selectTabsOption(event, index, setSO)}>{item}</span>)
            }
        </div>
        <div className="calList">
            <div className="callItems horizontalScrollTabs">
                {
                    fakeCourses.map((item, index) => <CourseCard props={randomCourse} key={index} />)
                }
                {<ScrollTabsNav props={{ container: '.callItems', scrollWidth: 215 }} />}
            </div>
        </div>
    </div>)
}

